@keyframes slide-up {
    from {
        transform: translateY(100%);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.animate-slide-up {
    animation: slide-up 0.5s ease-out forwards;
}


@keyframes slide-down {
    from {
        transform: translateY(-100%);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.animate-slide-down {
    animation: slide-down 0.5s ease-out forwards;
}



/* Dashboard.css */

/* Loader container */
.loader {
    border: 8px solid rgba(0, 0, 0, 0.1);
    /* Light background */
    border-left-color: rgb(249 115 22 / var(--tw-bg-opacity));
    /* Primary color for the spinning effect */
    border-radius: 50%;
    /* Make it circular */
    width: 50px;
    /* Loader width */
    height: 50px;
    /* Loader height */
    animation: spin 1s linear infinite;
    /* Spin animation */
    display: flex;
    /* Flex to center */
    justify-content: center;
    /* Center content */
    align-items: center;
    /* Center content */
}

/* Spin animation */
@keyframes spin {
    0% {
        transform: rotate(0deg);
        /* Start rotation */
    }

    100% {
        transform: rotate(360deg);
        /* End rotation */
    }
}


.paypal-popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    /* Scrollable popup */
    max-height: 80vh;
    /* Limit the height to viewport height */
    width: 50vw;
    /* Responsive width */
    overflow-y: auto;
    /* Enable vertical scrolling */
    overflow-x: hidden;
    /* Prevent horizontal overflow */
    z-index: 9999 !important;
}

.paypal-buttons-container {
    margin-top: 20px;
}

a {
    cursor: pointer;
}


.close-button {
    position: absolute;
    top: 1px;
    right: 15px;
    background: transparent;
    border: none;
    font-size: 28px;
    font-weight: bold;
    /* Make the button bold */
    cursor: pointer;
    color: #000;
    /* Optional: set a color for the button */
}


.title-Q_KPo8tL {
    font-size: 18px !important;
}

.active-button {
    background-color: rgba(200, 200, 200, 0.5);
    /* Adjust the color as needed */
    padding: 5px;
}


@keyframes slideIn {
    0% {
        transform: translateX(-100%);
        /* Start from the left side */
        opacity: 0;
        /* Start invisible */
    }

    100% {
        transform: translateX(0);
        /* End at original position */
        opacity: 1;
        /* Fully visible */
    }
}

.animated {
    animation: slideIn 0.5s ease-out;
    /* Animation duration and easing */
}

li {
    text-align: justify;
}



.chat-container {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin: 0 auto;
}

.message {
    display: flex;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    max-width: 80%;
}

.message-left {
    align-self: flex-start;
    background-color: #f1f0f0;
    color: #333;
}

.message-right {
    align-self: flex-end;
    background-color: #007bff;
    color: white;
}

.message-content {
    display: flex;
    flex-direction: column;
}

.message-right-date {
    font-size: 0.6rem;
    color: #DDD;
    margin-top: 5px;
}

.message-left-date {
    font-size: 0.6rem;
    color: black;
    margin-top: 5px;
}

.typing-effect {
    overflow: hidden;
    white-space: nowrap;
    display: inline-block;
    /* Ensure the element doesn't stretch */
    border-right: 3px solid transparent;
    /* Initially set to transparent */
    animation: typing 3.5s steps(30, end) 1, blink-caret 0.75s step-end 1s forwards;
}

@keyframes typing {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

@keyframes blink-caret {
    50% {
        border-color: transparent;
        /* Hide caret after typing */
    }
}


.dashboard-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
}

.chart-container {
    width: 48%;
    /* Each chart will take up roughly 50% of the width */
    height: 45vh;
    /* Each chart will take up 50% of the viewport height */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

h1 {
    text-align: center;
}